define([
    'skylark-threejs',
    'skylark-mrdoobui',
    '../../commands/SetGeometryCommand'
], function (THREE, mrdoobui, SetGeometryCommand) {
    'use strict';
    var SidebarGeometryRingGeometry = function (editor, object) {
        var strings = editor.strings;
        var container = new mrdoobui.UIRow();
        var geometry = object.geometry;
        var parameters = geometry.parameters;
        var innerRadiusRow = new mrdoobui.UIRow();
        var innerRadius = new mrdoobui.UINumber(parameters.innerRadius).onChange(update);
        innerRadiusRow.add(new mrdoobui.UIText(strings.getKey('sidebar/geometry/ring_geometry/innerRadius')).setWidth('90px'));
        innerRadiusRow.add(innerRadius);
        container.add(innerRadiusRow);
        var outerRadiusRow = new mrdoobui.UIRow();
        var outerRadius = new mrdoobui.UINumber(parameters.outerRadius).onChange(update);
        outerRadiusRow.add(new mrdoobui.UIText(strings.getKey('sidebar/geometry/ring_geometry/outerRadius')).setWidth('90px'));
        outerRadiusRow.add(outerRadius);
        container.add(outerRadiusRow);
        var thetaSegmentsRow = new mrdoobui.UIRow();
        var thetaSegments = new mrdoobui.UIInteger(parameters.thetaSegments).setRange(3, Infinity).onChange(update);
        thetaSegmentsRow.add(new mrdoobui.UIText(strings.getKey('sidebar/geometry/ring_geometry/thetaSegments')).setWidth('90px'));
        thetaSegmentsRow.add(thetaSegments);
        container.add(thetaSegmentsRow);
        var phiSegmentsRow = new mrdoobui.UIRow();
        var phiSegments = new mrdoobui.UIInteger(parameters.phiSegments).setRange(3, Infinity).onChange(update);
        phiSegmentsRow.add(new mrdoobui.UIText(strings.getKey('sidebar/geometry/ring_geometry/phiSegments')).setWidth('90px'));
        phiSegmentsRow.add(phiSegments);
        container.add(phiSegmentsRow);
        var thetaStartRow = new mrdoobui.UIRow();
        var thetaStart = new mrdoobui.UINumber(parameters.thetaStart * THREE.MathUtils.RAD2DEG).setStep(10).onChange(update);
        thetaStartRow.add(new mrdoobui.UIText(strings.getKey('sidebar/geometry/ring_geometry/thetastart')).setWidth('90px'));
        thetaStartRow.add(thetaStart);
        container.add(thetaStartRow);
        var thetaLengthRow = new mrdoobui.UIRow();
        var thetaLength = new mrdoobui.UINumber(parameters.thetaLength * THREE.MathUtils.RAD2DEG).setStep(10).onChange(update);
        thetaLengthRow.add(new mrdoobui.UIText(strings.getKey('sidebar/geometry/ring_geometry/thetalength')).setWidth('90px'));
        thetaLengthRow.add(thetaLength);
        container.add(thetaLengthRow);
        function update() {
            editor.execute(new SetGeometryCommand(editor, object, new THREE.RingBufferGeometry(innerRadius.getValue(), outerRadius.getValue(), thetaSegments.getValue(), phiSegments.getValue(), thetaStart.getValue() * THREE.MathUtils.DEG2RAD, thetaLength.getValue() * THREE.MathUtils.DEG2RAD)));
        }
        return container;
    };
    return SidebarGeometryRingGeometry;
});